<template>
  <div class="row">
    <div class="col-md-12">
      <card>
        <div slot="header">
          <h5 class="title">EDIT RENTAL</h5>
        </div>
        <form>
            <div class="row">
                <label class="col-md-2 col-form-label offset-md-3 text-right">TENANT</label>
                <div class="col-md-3">
                    <vue-single-select
                                v-model="rental.tenant"
                                :options="this.tenant"
                                :value="rental.tenant">
                    </vue-single-select>
                </div>
            </div>
            <div class="row">
                <label class="col-md-2 col-form-label offset-md-3 text-right">BLOCK NO</label>
                <div class="col-md-3">
                    <vue-single-select
                                v-model="rental.blockNo"
                                :options="this.block"
                                :value="rental.blockNo">
                    </vue-single-select>
                </div>
            </div>
            <div class="row">
                <label class="col-md-2 col-form-label offset-md-3 text-right">FEES</label>
                <div class="col-md-3">
                    <fg-input type="text" v-model="rental.fees" :value="rental.fees"></fg-input>
                </div>
            </div>
            <!--Date Picker-->
            <div class="row">
              <label class="col-md-2 col-form-label offset-md-3 text-right">START DATE</label>
                <div class="col-md-2">
                  <fg-input>
                    <el-date-picker v-model="rental.startDate" type="date" placeholder="Pick a day" value-format="yyyy-MM-dd" :picker-options="pickerOptions"></el-date-picker>
                  </fg-input>
                </div>
              <label class="col-form-label">EXPIRY DATE</label>
                <div class="col-md-2">
                  <fg-input>
                    <el-date-picker v-model="rental.expiryDate" type="date" placeholder="Pick a day" value-format="yyyy-MM-dd" :picker-options="pickerOptions"></el-date-picker>
                  </fg-input>
                </div>
            </div>
            <div class="row">
                <label class="col-md-2 col-form-label offset-md-3 text-right">STATUS</label>
              <div class="col-md-3">
                <n-radio class="col-md-1 display-inline" v-model="enabledRadio" label="PENDING">PENDING</n-radio>
                <n-radio class="col-md-1 display-inline" v-model="enabledRadio" label="PAID">PAID</n-radio>
              </div>
            </div>
            <div class="row">
                <div class="col-md-6 offset-md-4">
                <n-button class="button" type="info" @click.native="radio_status(enabledRadio); put();">Update</n-button>
                <a class="cancel">Cancel</a>
                <!-- <n-button class="button" type="danger">Cancel</n-button> -->
                </div>
            </div>
        </form>
      </card>
    </div>
  </div>
</template>

<script>
import { DatePicker } from "element-ui";
import { Radio } from "src/components";

export default {
  components: {
    [DatePicker.name]: DatePicker,
    [Radio.name]: Radio
  },
  data() {
    return {
      id: this.$route.params.id,
      rental: {
        tenant:"",
        blockNo:"",
        fees:"",
        startDate:"",
        expiryDate:"",
        status:""
      },
      enabledRadio: "PAID",
      pickerOptions: {
        shortcuts: [
          {
            text: "Today",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "Yesterday",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "A week ago",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      tenant:[],
      block: [],
      key_data: {
        tenant:"",
        blockNo:"",
        fees:"",
        startDate:"",
        expiryDate:"",
        status:""
      }
    };
  },
  methods: {
    //update the data to database
    put() {
      this.$http.put('https://admindashboard-b2ab9.firebaseio.com/rental/'+ this.id +'.json',this.rental)
    },
    radio_status(er){
      if (er == "PAID"){
        this.rental.status = 'PAID'; 
      }
      else{
        this.rental.status = 'PENDING';
      }
    }
  },
  created(){
    //get data to show in textboxes
    this.$http.get('https://admindashboard-b2ab9.firebaseio.com/rental.json').then(function(data){
      return data.json();
    }).then(function(data){
      //pass data to key_data object
      this.key_data = data
      //set the fields accordingly with the unique id
      this.rental.tenant = this.key_data[this.id].tenant
      this.rental.fees = this.key_data[this.id].fees
      this.rental.blockNo = this.key_data[this.id].blockNo
      this.rental.status = this.key_data[this.id].status
      this.rental.startDate = this.key_data[this.id].startDate
      this.rental.expiryDate = this.key_data[this.id].expiryDate

      //get tenant name from tenant table
      this.$http.get('https://admindashboard-b2ab9.firebaseio.com/tenant.json').then(function(data){
        return data.json(); 
      }).then(function(data){
        var tenant_Array = [];
          for (let key in data){
            tenant_Array.push(data[key].name);
          }
        this.tenant = tenant_Array;

        //get blockNo from block table
        this.$http.get('https://testing-36bc3.firebaseio.com/block.json').then(function(data){
          return data.json();
        }).then(function(data){
            var block_Array = [];
            for (let key in data){
              block_Array.push(data[key].blockNo);
            }
          this.block = block_Array;
          })
        
        //toggle radio button
        if ( this.rental.status == 'PAID'){
          this.enabledRadio = 'PAID'
        }else{
          this.enabledRadio = 'PENDING'
        }
      })
    })
  }
};
</script>
<style scoped>
.display-inline {
  display: inline !important;
}
.cancel{
    /* display: inline; */
    margin-left: 20px;
    color: red;
    text-decoration: underline !important;
}
</style>
